@import url(<link href="https://fonts.googleapis.com/css?family=Roboto:300,400,500,700,900&display=swap" rel="stylesheet">
);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  
}


#root > div > div > nav{
    display: grid;
    font-family: 'Roboto', sans-serif;
    grid-template-columns: 1fr auto;
    padding: 0px 140px;
}

#root > div > div > nav > a{
    font-family: 'Roboto', sans-serif;
    font-size: 32px;
    line-height: 38px;
    text-transform: uppercase;
    color: #361c32;
    display: flex;
}

#basic-navbar-nav{
    font-size: 32px;
    justify-self: end;
}

#basic-navbar-nav > div > a{
    border-top: 8px solid transparent;
    font-size: 32px;
    line-height: 38px;
    color: #361c32;
    font-family: 'Roboto', sans-serif;
    padding: 56px 24px;
    -webkit-transition: 0.1s ease-in-out;
    transition: 0.1s ease-in-out;
}

#basic-navbar-nav > div > a:hover{
    border-top: 8px solid #f55951;
}

/* Media querry */



@media screen and (max-width: 320px) {
    #root > div > div > nav{
        padding: 0.5em 1em;
    }

    #root > div > div > nav > a{
        font-size: 4vw;
    }

    #root > div > div:nth-child(1) > nav > button{
        padding: 0px;
        width: 30px;
        justify-self: end;
    }

    #basic-navbar-nav > div{
        display: grid;
        grid-template-columns: repeat(4, 1fr);
    }
    #basic-navbar-nav > div > a:nth-child(n){
        font-size: 3vw;
        padding: 0px 5px 0px 0px;
    }

    #basic-navbar-nav{
        -webkit-transition: 0.4s ease-in-out;
        transition: 0.4s ease-in-out;
    }
}
.component__align__right{
    display: grid;
    grid-template-columns: 1fr 1.1fr;
    grid-template-rows: 885px;
    grid-gap: 0px 32px;
    padding: 0px 0px 0px 140px;
    overflow: hidden;
}

.component__align__right_content_left{
    align-self: center;
    color: #361c32;
    font-size: 64px;
    font-family: 'Roboto', sans-serif;
    font-weight: bold;
    line-height: 1.22;
}

.component__align__right_content_right{
    background-image: url(/baciutudorstefan/test-page/static/media/wcap.c3735f01.jpg);
    background-repeat: no-repeat;
    background-position: center top;
    background-size: cover;
}

/* Media querry */

@media screen and (max-width: 320px) {
    .component__align__right {
        grid-template-areas: 
        "promote-title promote-title"
        "promote-image promote-image"
        !important;
        grid-gap: 0px !important;
        grid-template-rows: 90px 300px;
        padding: 0px;
    }

    .component__align__right_content_left{
        font-size: 4vw;
        grid-area: promote-title;
        padding: 0px 2em;
        text-align: center;
    }

    .component__align__right_content_right{
        grid-area: promote-image;
        background-size: cover;
        background-position: top;
    }


}
.component__align__center{
    display: grid;
    font-family: 'Roboto', sans-serif;
    font-size: 32px;
    grid-template-columns: 1fr 2fr;
    grid-gap: 0px 32px;
    padding: 0px 140px;
    margin-top: 148px;
    overflow: hidden;
}

.component__align__center_title{
    color: #f55951;
    font-size: 2.18vw;
}

.component__align__center_paragraphs{
    display: grid;
    grid-row-gap: 50px;
}
.component__align__center_paragraphs > p{
    line-height: 1.5;
}

@media screen and (max-width: 320px) {
    .component__align__center{
        margin-top: 90px;
    }
    .component__align__center_title{
        color: #f55951;
        font-size: 5.73vw;
        text-align: center;
    }
    .component__align__center_paragraphs{
        padding: 0px 1em;
        font-size: 3.27vw;
    }

    #root > div > div.component__align__center > div > p:nth-child(n){
        text-align: center;
    }
}



/* CSS Anim */

#root > div > div:nth-child(3) > div.component__align__center_paragraphs > div:nth-child(1){
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
    -webkit-transition: ease-in;
    transition: ease-in;
}

#root > div > div:nth-child(3) > div.component__align__center_paragraphs > div:nth-child(2){
    -webkit-animation-duration: 0.8s;
            animation-duration: 0.8s;
    -webkit-transition: ease-in;
    transition: ease-in;

}

#root > div > div:nth-child(3) > div.component__align__center_paragraphs > div:nth-child(3){
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-transition: ease-in;
    transition: ease-in;

}
